@mixin section-margin {
  margin-left: 8.05%;
  margin-right: 8.05%;
  width: calc(100% - 16.1%);

  @include portrait {
    margin-left: $lem;
    margin-right: $lem;
    width: calc(100% - #{$lem * 2});
  }

  @include tablet-landscape {
    margin-left: $lem * 2;
    margin-right: $lem * 2;
    width: calc(100% - #{$lem * 4});
  }
}
