@mixin rich-media-hero__specs--mobile {
  @include mobile {
    @include rich-media-hero--reposition(250px, .8, 6.7em, 0em, '.text');

    .text {
      font-size: smaller;
      padding: $lem / 2;

      p {
        display: none;

        &:first-of-type {
          display: flex;
        }
      }
    }
  }
}
