@mixin section-padding {
  padding-left: 8.05%;
  padding-right: 8.05%;
  width: calc(100% - 16.1%);

  @include portrait {
    padding-left: $lem;
    padding-right: $lem;
    width: calc(100% - #{$lem * 2});
  }

  @include tablet-landscape {
    padding-left: $lem * 2;
    padding-right: $lem * 2;
    width: calc(100% - #{$lem * 4});
  }
}
