@mixin hero-mockup--reposition {
  &--tablet {
    top: $lem * 7;
  }

  &--phone {
    transform: translateY($lem * 9);
  }


  @include mobile {
    &--tablet {
      top: 0em;
    }

    &--phone {
      transform: translateY(-2em);
    }
  }

  @include tablet {
    &--tablet {
      top: $lem * 3.5;
    }

    &--phone {
      transform: translateY(5em);
    }
  }

  @include tablet-landscape {
    padding: 0 $lem;

    &--tablet {
      top: $lem * 5;

      .img {
        width: 524px;
      }
    }

    &--phone {
      transform: translateY($lem * 5);

      .img {
        width: 164px;
      }
    }
  }


  @include desktop {
    &--phone {
      transform: translateY(12em);
    }
  }


  @include fullhd {
    &--tablet {
      top:12em;
    }

    &--phone {
      transform: translateY(15em);
    }
  }
}
